import {mapActions} from 'vuex';
export default {
  name: 'SubItem',
  props: ['item'],
  data() {
    return {};
  },
  methods: {
    toDoRegister: function() {
      this.$emit('doregister');
    },
    toPayBill: function() {
      this.$router.push({
        path: this.$routes.JFJL.path
      });
    },
    deleteSubscribe: function(id) {
      this.$emit('delete', id);
    },
    showDetails: function () {
      this.$router.push({
        path: this.$routes.YYXQ.path
      });
    },
    ...mapActions([
      'exeDeleteSubscribeList'
    ])
  }
};



