import {mapActions} from 'vuex';

const controllers = {
  showMenus(type) {
    this.type = type;
    this.popupPicker.show = true;
    if (type === '') {
      this.popupPicker.data = [['', '']];
    } else if (type === '') {
      this.popupPicker.data = [[
        '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '',
        '', '', '', '', ''
      ]];
    } else if (type === '') {
      this.popupPicker.data = [[
        '', '', '',
        '', ''
      ]];
    }
  },
  checkNotEmpty() {
    this.phone = this.phone ? this.phone : '';
    if (this.name && this.number &&
      this.gender && this.nation &&
      this.phone && this.relation &&
      this.gender !== '' &&
      this.nation !== '' &&
      this.phone !== '' &&
      this.relation !== '') {
      this.status = '';
    } else {
      this.status = 'disabled';
    }
  },
  showKeyBoard(type) {
    this.isShow = true;
    this.TYPE = type;
    if (this.TYPE === 'phone') {
      this.initValue = this.phone === '' ? '' : this.phone;
    }
  },
  writeNumber(value) {
    if (this.TYPE === 'phone') {
      this.phone = value;
    }
  },
  handlerChange(value) {
    if (this.type === '') {
      this.gender = value[0];
    } else if (this.type === '') {
      this.nation = value[0];
    } else if (this.type === '') {
      this.relation = value[0];
    }
  },
  exeInsertVisitorList() {
    if (!this.status) {
      const data = {
        name: this.name,
        idCardNo: this.number,
        sex: this.gender,
        nation: this.nation,
        releation: this.relation,
        phone: this.phone
      };
      this.insertVisitorList(data)
        .then((res) => {
          const {data, success} = res;
          if (data && success) {
            this.$vux.toast.show({
              text: ''
            });
            this.clearFormData();
            this.$router.back();
          } else {
            this.$vux.toast.show({
              text: ''
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  },
  clearFormData() {
    this.name = '';
    this.number = '';
    this.phone = '';
    this.gender = '';
    this.nation = '';
    this.relation = '';
  },
  ...mapActions([
    'insertVisitorList',
    'updateVisitorList'
  ])
};

export default controllers;



