import {mapState} from 'vuex';
import Controller from './Controller';
import SubItem from '../../components/SubItem';
import NoData from '../../components/NoData';
import Error from '../../components/Error';
import MescrollVue from 'mescroll.js/mescroll.vue';

export default {
  components: {
    NoData,
    Error,
    SubItem,
    MescrollVue
  },
  name: 'WDYY',
  data() {
    return {
      pageCode: 1,
      mescroll: null,
      up: {
        auto: false,
        isBounce: true,
        callback: this.infinite,
        htmlNodata: '<p class="upwarp-nodata"></p>'
      },
      down: {
        auto: false,
        offset: 50,
        mustToTop: true,
        outOffsetRate: 0.3,
        callback: this.refresh,
        autoShowLoading: true
      }
    };
  },
  created() {
    this.CLEAR_REGISTER_LIST_SUCCESS();
    this.exeSelectSubscribeList();
  },
  methods: Controller,
  computed: mapState({
    isLoading: state => state.SUBSCRIBE_LIST.isLoading,
    isSuccess: state => state.SUBSCRIBE_LIST.isSuccess,
    isFailure: state => state.SUBSCRIBE_LIST.isFailure,
    data: state => state.SUBSCRIBE_LIST.data
  })
};



