export default {
  name: 'KeyBoard',
  data() {
    return {
      value: '',
      keyCodes: [
        {
          keyCode: 1,
          text: '1'
        },
        {
          keyCode: 2,
          text: '2'
        },
        {
          keyCode: 3,
          text: '3'
        },
        {
          keyCode: 4,
          text: '4'
        },
        {
          keyCode: 5,
          text: '5'
        },
        {
          keyCode: 6,
          text: '6'
        },
        {
          keyCode: 7,
          text: '7'
        },
        {
          keyCode: 8,
          text: '8'
        },
        {
          keyCode: 9,
          text: '9'
        },
        {
          keyCode: 10,
          text: ''
        },
        {
          keyCode: 0,
          text: '0'
        },
        {
          keyCode: 11,
          text: '<p class="icon-delete"></p>'
        }
      ]
    };
  },
  props: ['isShow', 'initValue'],
  methods: {
    hideKeyBoard: function () {
      this.$emit('update:isShow', false);
    },
    touchMove: function (ev) {
      ev.preventDefault();
    },
    clickNumber: function (keyCode) {
      switch (keyCode) {
        case 1:
        case 2:
        case 3:
        case 4:
        case 5:
        case 6:
        case 7:
        case 8:
        case 9:
        case 0:
          this.value += keyCode + '';
          break;
        case 10:
          break;
        case 11:
          const len = this.value.length;
          this.value = len ? this.value.substring(0, (len - 1)) : '';
          break;
      }
      this.$emit('writeNumber', this.value);
    }
  },
  computed: {
    getIsShow: function () {
      return this.isShow;
    }
  },
  watch: {
    isShow: function () {
      if (this.isShow) {
        this.value = this.initValue;
      }
    }
  }
};



