import Controller from './Controller';
import VisitItem from '../../components/VisitItem';
import NoData from '../../components/NoData';
import MescrollVue from 'mescroll.js/mescroll.vue';

export default {
  components: {
    VisitItem,
    NoData,
    MescrollVue
  },
  name: 'DJCLB',
  data() {
    return {
      items: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}],
      dataList: [],
      isShow: false,
      mescroll: null,
      up: {
        auto: false,
        isBounce: false,
        callback: this.infinite,
        htmlNodata: '<p class="upwarp-nodata"></p>'
      },
      down: {
        auto: true,
        offset: 50,
        mustToTop: true,
        outOffsetRate: 0.3,
        callback: this.refresh,
        autoShowLoading: true
      }
    };
  },
  methods: Controller,
  mounted() {
    this.pageCode = 1;
  }
};



