
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Controller from './Controller';
import KeyBoard from '../../components/KeyBoard';
import {getIndexOf} from '../../utils';

export default {
components: {
KeyBoard,
PopupPicker
},
name: 'JKDA',
data() {
return {
TYPE: '',
isShow: false,
initValue: '',
id: 0,
cardNo: '1',
height: '',
weight: '',
bloodTypeIndex: '',
smokingIndex: '',
drinkIndex: '',
status: 'disabled',
bloodArr: ['A', 'B', 'AB', 'O'],
isSmokeArr: ['', ''],
isDrink: ['', ''],
popupPicker: {
show: false,
data: [['']],
value: null,
popupTitle: ''
}
};
},
computed: {
blood: {
get: function () {
return this.bloodArr[this.bloodTypeIndex - 1];
},
set: function (newValue) {
this.bloodTypeIndex = getIndexOf(this.bloodArr, newValue) + 1;
}
},
smoke: {
get: function () {
return this.isSmokeArr[this.smokingIndex - 1];
},
set: function (newValue) {
this.smokingIndex = getIndexOf(this.isSmokeArr, newValue) + 1;
}
},
wine: {
get: function () {
return this.isDrink[this.drinkIndex - 1];
},
set: function (newValue) {
this.drinkIndex = getIndexOf(this.isDrink, newValue) + 1;
}
}
},
methods: Controller,
created() {
if (this.isLoading) return;
this.exeSelectHealthList();
},
watch: {
height() {
this.checkNotEmpty();
},
weight() {
this.checkNotEmpty();
},
blood() {
this.checkNotEmpty();
},
smoke() {
this.checkNotEmpty();
},
wine() {
this.checkNotEmpty();
}
}
};



