export default {
  name: 'RegItem',
  data() {
    return {};
  },
  props: ['item'],
  methods: {
    toPayBill: function() {
      this.$router.push({
        path: this.$routes.JFJL.path
      });
    },
    deleteSubscribe: function(id) {
      this.$emit('delete', id);
    },
    showDetails: function () {
      this.$router.push({
        path: this.$routes.GHXQ.path
      });
    }
  }
};



