export default {
  name: 'NavItem',
  data() {
    return {};
  },
  props: [
    'item'
  ],
  methods: {
    handlerClick: function () {
      if (!this.item.disabled) {
        this.$router.push({
          path: this.item.path,
          query: this.item.query
        });
      } else {
        this.$vux.toast.show({
          type: 'text',
          text: '',
          position: 'middle'
        });
      }
    }
  }
};



