
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Controller from './Controller';
import KeyBoard from '../../components/KeyBoard';

export default {
components: {
KeyBoard,
PopupPicker
},
name: 'SFLR',
data() {
return {
TYPE: '',
isShow: false,
initValue: '',
name: '',
number: '',
phone: '',
gender: '',
nation: '',
relation: '',
status: 'disabled',
popupPicker: {
show: false,
data: [['']],
value: null,
popupTitle: ''
}
};
},
methods: Controller,
watch: {
name() {
this.checkNotEmpty();
},
number() {
this.checkNotEmpty();
},
phone() {
this.checkNotEmpty();
},
gender() {
this.checkNotEmpty();
},
nation() {
this.checkNotEmpty();
},
relation() {
this.checkNotEmpty();
}
}
};



