
import {mapState} from 'vuex';
import Controller from './Controller';
import Error from '../../components/Error';
import NoData from '../../components/NoData';
import MescrollVue from 'mescroll.js/mescroll.vue';
import SufferItem from '../../components/SufferItem';
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
components: {
NoData,
Error,
Swipeout,
SufferItem,
SwipeoutItem,
SwipeoutButton,
MescrollVue
},
name: 'HZLB',
data() {
return {
pageCode: 1,
mescroll: null,
up: {
auto: false,
isBounce: true,
callback: this.infinite,
htmlNodata: '<p class="upwarp-nodata"></p>'
},
down: {
auto: false,
offset: 50,
mustToTop: true,
outOffsetRate: 0.3,
callback: this.refresh,
autoShowLoading: true
}
};
},
created() {
this.CLEAR_VISITOR_LIST_SUCCESS();
this.exeSelectVisitorList();
},
methods: Controller,
computed: mapState({
isLoading: state => state.VISITOR_LIST.isLoading,
isSuccess: state => state.VISITOR_LIST.isSuccess,
isFailure: state => state.VISITOR_LIST.isFailure,
data: state => state.VISITOR_LIST.data
})
};



