export default {
  name: 'DoctorItem',
  data() {
    return {};
  },
  props: ['item', 'isPre'],
  methods: {
    handlerClick(doctCode) {
      const {query} = this.$route || {};
      const newQuery = {doctCode: doctCode};
      const params = Object.assign(query, newQuery);
      this.$router.push({
        path: this.$routes.YSZY.path,
        query: params
      });
    }
  }
};



